@use 'styles/colors';
@import 'styles/base';

:host {
  display: block;

  ::ng-deep {
    [empty-text] {
      @include proxima(16px, 600, colors.$foreground_dark);
    }

    [list-item] {
      display: flex;
      flex-wrap: wrap;
      padding: 14px;
      background-color: #f2f2f2;
      gap: 10px;

      &:not(:last-child) {
        border-bottom: 1px solid #E8E8E8;
      }

      &:last-child {
        margin-bottom: 10px;
      }
    }

    [list-item-section] {
      display: flex;
      align-items: center;
      gap: 8px;

      &:first-child {
        flex-grow: 1;
        flex-shrink: 0;
        padding: 0 8px;
        background-color: #FFFFFF;
      }
    }

    [list-item-mark] {
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: #ee2a17;
      margin: 0 6px;
      flex-shrink: 0;
    }

    [list-item-text] {
      @include proxima(16px, 600, colors.$foreground_dark);
    }

    [list-item-button] {
      flex-grow: 1;
    }

    @media screen and (max-width: 500px) {
      [list-item] {
        flex-direction: column;
      }

      [list-item-section] {
        &:first-child {
          padding: 0 8px;
          height: 40px;
        }

        &:last-child {
          justify-content: center;
          gap: 60px;
        }
      }
    }
  }
}
